.sm\:max-w-lg {
    max-width: 32rem;
}

.sm\:order-last {
    order: 9999;
}

.sm\:items-start {
    align-items: flex-start;
}

.sm\:flex {
    display: flex;
}

.sm\:grid-cols-none {
    grid-template-columns: none;
}

.sm\:gap-x-3 {
    -moz-column-gap: .75rem;
    column-gap: .75rem;
}

.sm\:justify-end {
    justify-content: flex-end;
}

.gap-y-2 {
    row-gap: .5rem;
}

.sm\:bg-secondary-100 {
    --tw-bg-opacity: 1;
    background-color: rgb(241 245 249/var(--tw-bg-opacity));
}

.sm\:pt-16 {
    padding-top: 4rem;
}

.justify-center {
    justify-content: center;
}

.items-end {
    align-items: flex-end;
}
